import React from 'react';
import { TooltipData } from './types';

function Tooltip({ tooltip}: TooltipData) {
  //  0, null,  undefined   
  const shouldRender = (value: string | number | boolean | null | undefined): {__html: string} | undefined => {
    if (typeof value === 'string' && value !== '0' && value !== '') {
      //   : newline  <br>  
      return { __html: value.replace(/\n/g, '<br>') };
    } else if (value !== null && value !== undefined && value !== '0' && value !== 0) {
      // value boolean, number   :  
      return { __html: String(value) };
    }
    //    : undefined 
    return undefined;
  };
  
  
  function getOptionGradeColor(grade: string | undefined) {
    switch (grade) {
      case '':
       return 'skyblue';
      case '':
        return '#d09aff'; //
      case '':
        return 'gold'; 
      case '':
        return 'lightgreen'; 
      default:
        return 'white';
    }
  }

  return (
    <div
      className="tooltip"
      style={{
        position: 'fixed',
        left: '50%',
        top: '50%',
        transform: 'translate(-50%, -50%)'
      }}
    >
      {shouldRender(tooltip.starforce) && <div style={{color:'yellow'}}>{tooltip.starforce}</div>}
      {<div style={{color:'pink'}}><strong>{tooltip.item_name}(+{tooltip.scroll_upgrade})</strong></div>}
      {tooltip.potential_option_grade && <div>({tooltip.potential_option_grade})</div>}
      {tooltip.item_shape_icon && <img src={tooltip.item_shape_icon} alt="item" />}
      {shouldRender(tooltip.item_description) && <div dangerouslySetInnerHTML={shouldRender(tooltip.item_description)}></div>}
      {shouldRender(tooltip.item_total_option['str']) && <div style={{color:'#46D2D2'}}>STR : +{tooltip.item_total_option['str']}()</div>}
      {shouldRender(tooltip.item_total_option['dex']) && <div style={{color:'#46D2D2'}}>DEX : +{tooltip.item_total_option['dex']}()</div>}
      {shouldRender(tooltip.item_total_option['int']) && <div style={{color:'#46D2D2'}}>INT : +{tooltip.item_total_option['int']}()</div>}
      {shouldRender(tooltip.item_total_option['luk']) && <div style={{color:'#46D2D2'}}>LUK : +{tooltip.item_total_option['luk']}()</div>}
      {shouldRender(tooltip.item_total_option['max_hp']) && <div style={{color:'#46D2D2'}}>HP : +{tooltip.item_total_option['max_hp']}()</div>}
      {shouldRender(tooltip.item_total_option['max_mp']) && <div style={{color:'#46D2D2'}}>MP : +{tooltip.item_total_option['max_mp']}()</div>}
      {shouldRender(tooltip.item_total_option['attack_power']) && <div style={{color:'#46D2D2'}}> : +{tooltip.item_total_option['attack_power']}()</div>}
      {shouldRender(tooltip.item_total_option['magic_power']) && <div style={{color:'#46D2D2'}}> : +{tooltip.item_total_option['magic_power']}()</div>}
      {shouldRender(tooltip.item_total_option['armor']) && <div style={{color:'#46D2D2'}}> : +{tooltip.item_total_option['armor']}()</div>}
      {shouldRender(tooltip.item_total_option['speed']) && <div style={{color:'#46D2D2'}}> : +{tooltip.item_total_option['speed']}()</div>}
      {shouldRender(tooltip.item_total_option['jump']) && <div style={{color:'#46D2D2'}}> : +{tooltip.item_total_option['jump']}()</div>}
      {shouldRender(tooltip.item_total_option['boss_damage']) && <div style={{color:'#fff'}}>    (%) : +{tooltip.item_total_option['boss_damage']}()</div>}
      {shouldRender(tooltip.item_total_option['ignore_monster_armor']) && <div style={{color:'#fff'}}>  (%) : +{tooltip.item_total_option['ignore_monster_armor']}()</div>}
      {shouldRender(tooltip.item_total_option['all_stat']) && <div style={{color:'#fff'}}>(%) : +{tooltip.item_total_option['all_stat']}()</div>}
      {shouldRender(tooltip.item_total_option['damage']) && <div style={{color:'#fff'}}>(%) : +{tooltip.item_total_option['damage']}()</div>}
      {shouldRender(tooltip.item_total_option['equipment_level_decrease']) && <div style={{color:'#fff'}}>   : -{tooltip.item_total_option['equipment_level_decrease']}()</div>}
      {shouldRender(tooltip.item_total_option['max_hp_rate']) && <div style={{color:'#fff'}}> HP(%) : +{tooltip.item_total_option['max_hp_rate']}()</div>}
      {shouldRender(tooltip.item_total_option['max_mp_rate']) && <div style={{color:'#fff'}}> MP(%) : +{tooltip.item_total_option['max_mp_rate']}()</div>}
      {shouldRender(tooltip.golden_hammer_flag) && <div style={{color:'#fff'}}>  {tooltip.golden_hammer_flag}</div>}
      {shouldRender(tooltip.cuttable_count) && <div style={{color:'gold'}}>    : {tooltip.cuttable_count}</div>}
      <div className='spot-line'></div>
      {shouldRender(tooltip.potential_option_grade) && <div style={{ color: getOptionGradeColor(tooltip.potential_option_grade) }}>
        : {tooltip.potential_option_grade}
      </div>}
      {tooltip.potential_option_1 && <div>{tooltip.potential_option_1}</div>}
      {tooltip.potential_option_2 && <div>{tooltip.potential_option_2}</div>}
      {tooltip.potential_option_3 && <div>{tooltip.potential_option_3}</div>}
      <div className='spot-line'></div>
      {shouldRender(tooltip.additional_potential_option_grade) && <div style={{ color: getOptionGradeColor(tooltip.additional_potential_option_grade) }}>
         : {tooltip.additional_potential_option_grade}
      </div>}
      {tooltip.potential_option_1 && <div>{tooltip.additional_potential_option_1}</div>}
      {tooltip.potential_option_2 && <div>{tooltip.additional_potential_option_2}</div>}
      {tooltip.potential_option_3 && <div>{tooltip.additional_potential_option_3}</div>}
    </div>
  );
}

export default Tooltip;
