// features/myApi/myApiSlice.js
import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { fetchCharacterId, fetchCharacterData as fetchCharacterDetails,fetchItemData as fetchItemApi, fetchItemSet_EffectData as fetchItemSetEffectData, fetchCharacterStat } from './mapleStoryApi.ts';
import {ItemData, RootState,MyApiState,CharacterData} from '../../component/types.ts'
import { getYesterdayDate } from '../../utils/util.ts'

//       API  
const yesterdayDate = getYesterdayDate();

export const fetchCharacterData = createAsyncThunk<CharacterData | null, string>(
  'myApi/fetchCharacterData',
  async (characterName, { dispatch }) => {
    try {
      const data1 = await fetchCharacterId(characterName);
      console.log("First Response:", data1);

      if (data1 && data1.ocid) {
        dispatch(setOcid(data1.ocid));

        const data2 = await fetchCharacterDetails(data1.ocid, yesterdayDate);
        console.log("Second Response:", data2);  
        return data2;
      } else {
        throw new Error("Invalid ocid or no ocid in first response");
      }
    } catch (error) {
      throw error;
    }
  }
);

export const fetchItemSet_EffectData = createAsyncThunk<any, void>(
  'myApi/fetchItemSet_EffectData',
  async (_, { getState }) => {
    try {
      const state = getState() as RootState; // RootState  ;
      const ocid: string = state.myApi.ocid;
      const data = await fetchItemSetEffectData(ocid, getYesterdayDate());
      const data2 = await fetchCharacterStat(ocid, getYesterdayDate());
      //    .
      const mergeData = {
        set: data,    // 'set'  data 
        detail: data2 // 'detail'  data2 
      };
      if (mergeData) {
        //console.log(mergeData)
        return mergeData;
      } else {
        throw new Error("Invalid ocid or no ocid in first response");
      }
    } catch (error) {
      throw error;
    }
  }
);

