function calculateGridArea(itemSlot) {
  const gridAreaMap = {
    '4': "1 / 1 / 1 / 2",
    '3': "2 / 1 / 2 / 2",
    '2': "3 / 1 / 3 / 2",
    '1': "4 / 1 / 4 / 2",
    ' ': "5 / 1 / 5 / 2",

    '2': "2 / 2 / 2 / 2",
    '': "3 / 2 / 3 / 2",
    '': "4 / 2/ 4 / 2",
    '': "5 / 2 / 5 / 2",

    '': "1 / 3 / 1 / 3",
    '': "2 / 3 / 2 / 3",
    '': "3 / 3 / 3 / 3",
    '': "4 / 3 / 4 / 3",
    '': "5 / 3 / 5 / 3",
    '': "6 / 3 / 6 / 3",
    
    //'': "1 / 4 / 1 / 4",
    '': "3 / 4 / 3 / 4",
    '': "4 / 4 / 4 / 4",
    '': "5 / 4 / 5 / 4",
    //'': "6 / 4 / 6 / 4",

    '': "1 / 5 / 1 / 5",
    '': "2 / 5 / 2 / 5",
    '': "3 / 5 / 3 / 5",
    '': "4 / 5 / 4 / 5",
    '': "5 / 5 / 5 / 5",
    ' ': "6 / 5 / 6 / 5",
    //    
  };

  return gridAreaMap[itemSlot] || ""; //  
}

export const fetchItemData = createAsyncThunk<ItemData | null, void>(
  'myApi/fetchItem',
  async (_, { getState }) => {
    try {
      const state = getState() as RootState; // RootState  ;;
      const ocid = state.myApi.ocid;
      const data = await fetchItemApi(ocid, getYesterdayDate());
      
      if (data && data.item_equipment) {
        for (let i = 0; i < data.item_equipment.length; i++) {
          data.item_equipment[i].gridArea = calculateGridArea(data.item_equipment[i].item_equipment_slot);
        }
      }
      return data;
    } catch (error) {
      throw error; //  
    }
  }
);


//  ...
export const myApiSlice = createSlice({
    name: 'myApi',
    initialState: {
      data: null,
      item: null, //    
      setItem: null,
      ocid: "", // ocid  
      loading: false,
      error: null,
    } as MyApiState,
    reducers: {
      setOcid: (state, action: PayloadAction<string>) => { // ocid  reducer 
        state.ocid = action.payload;
      },
    },
    extraReducers: (builder) => {
      builder
      .addCase(fetchCharacterData.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(fetchCharacterData.fulfilled, (state, action) => {
        state.loading = false;
        state.data = action.payload;
      })
      .addCase(fetchCharacterData.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message ?? null;
        state.data = null; //     
      })
      .addCase(fetchItemData.pending, (state) => {
        state.loading = true;
      })
      .addCase(fetchItemData.fulfilled, (state, action) => {
        state.loading = false;
        state.item = action.payload; //   
      })
      .addCase(fetchItemData.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message ?? null;
      })
      .addCase(fetchItemSet_EffectData.pending, (state) => {
        state.loading = true;
      })
      .addCase(fetchItemSet_EffectData.fulfilled, (state, action) => {
        state.loading = false;
        state.setItem = action.payload; //     
      })
      .addCase(fetchItemSet_EffectData.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message ?? null;
      });
      // .addCase(fetchHyperStatData.pending, (state) => {
      //   state.loading = true;
      // })
      // .addCase(fetchHyperStatData.fulfilled, (state, action) => {
      //   state.loading = false;
      //   state.setHyperStat = action.payload; //   
      // })
      // .addCase(fetchHyperStatData.rejected, (state, action) => {
      //   state.loading = false;
      //   state.error = action.error.message ?? null;
      // });
    },
  });
  export const { setOcid } = myApiSlice.actions; //   
  export default myApiSlice.reducer;